<template>
    <div class="page-404">
        <div class="wscn-http404">
            <div class="bullshit">
                <div class="bullshit__oops">错误!</div>
                <div class="bullshit__info">Copyright&nbsp©&nbsp2023<a class="link-type" href="#" target='_blank'>wnuwer</a>
                </div>
                <div class="bullshit__headline">{{ message }}</div>
                <div class="bullshit__info">请检查您输入的网址是否正确，请点击以下按钮返回主页或者发送错误报告</div>
                <a @click="pathToIndex" class="bullshit__return-home">返回首页</a>
            </div>
        </div>
    </div>
</template>
  
<script>

export default {
    data() {
        return {

        }
    },
    computed: {
        message() {
            return '页面没找到 - _ -\......'
        }
    },
    methods: {
        pathToIndex() {
            this.$router.push("/index");
            console.log(this)
        }
    }
}
</script>
  
<style rel="stylesheet/scss" lang="scss" scoped>
.page-404 {
    width: 100%;
    height: 100%;
    background: rgba(240, 242, 245, 0.9);
    margin-top: 0px;
}

.wscn-http404 {
    width: 100%;
    height: 100%;
    overflow: hidden;

    .bullshit {
        min-width: 50%;
        width: 50%;
        padding: 150px 0;
        overflow: hidden;
        margin-left: 25%;

        &__oops {
            font-size: 32px;
            font-weight: bold;
            line-height: 40px;
            color: #d31e36;
            opacity: 0;
            margin-bottom: 20px;
            animation-name: slideUp;
            animation-duration: 0.5s;
            animation-fill-mode: forwards;
        }

        &__headline {
            font-size: 20px;
            line-height: 24px;
            color: #1482f0;
            opacity: 0;
            margin-bottom: 10px;
            animation-name: slideUp;
            animation-duration: 0.5s;
            animation-delay: 0.1s;
            animation-fill-mode: forwards;
        }

        &__info {
            font-size: 13px;
            line-height: 21px;
            color: grey;
            opacity: 0;
            margin-bottom: 30px;
            animation-name: slideUp;
            animation-duration: 0.5s;
            animation-delay: 0.2s;
            animation-fill-mode: forwards;
        }

        &__return-home {
            display: block;
            float: left;
            width: 110px;
            height: 36px;
            background: #1482f0;
            border-radius: 100px;
            text-align: center;
            color: #ffffff;
            opacity: 0;
            font-size: 14px;
            line-height: 36px;
            cursor: pointer;
            animation-name: slideUp;
            animation-duration: 0.5s;
            animation-delay: 0.3s;
            animation-fill-mode: forwards;
        }

        @keyframes slideUp {
            0% {
                transform: translateY(60px);
                opacity: 0;
            }

            100% {
                transform: translateY(0);
                opacity: 1;
            }
        }
    }
}
</style>


